"use client";

import { useState, useEffect, useMemo } from "react";
import { useQuery } from "@tanstack/react-query";
import { fetchNotesByTag, NoteResponse } from "@/lib/api";
import NotesList from "@/components/NoteList/NoteList";
import Pagination from "@/components/Pagination/Pagination";
import Loading from "../../app/loading";
import ErrorComponent from "../../app/notes/error";

interface NotesPageProps {
  tag: string; // приходит из FilteredNotesPage
}

export default function NotesPage({ tag }: NotesPageProps) {
  const [page, setPage] = useState<number>(1);

  // Сбрасываем страницу на 1 при смене тега
 useEffect(() => {
  const t = setTimeout(() => setPage(1), 0);
  return () => clearTimeout(t);
}, [tag]);

  // useQuery с зависимостью от тега и страницы
  const queryKey = useMemo(() => ["notes", tag, page], [tag, page]);
  const { data, isLoading, error, isFetching } = useQuery<NoteResponse, Error>({
    queryKey,
    queryFn: () => fetchNotesByTag(tag, page, 12),
    staleTime: 1000 * 5,
  });

  if (isLoading) return <Loading />;
  if (error || !data)
    return <ErrorComponent error={error ?? new Error("No data")} reset={() => setPage(1)} />;

  const notes = data.notes ?? [];
  const totalPages = data.totalPages ?? 1;

  return (
    <div style={{ display: "flex", flexDirection: "column", gap: "16px" }}>
      {isFetching && <span>Loading…</span>}

      <NotesList notes={notes} />

      {totalPages > 1 && (
        <Pagination
          currentPage={page}
          totalPages={totalPages}
          onPageChange={(p) => setPage(p)}
        />
      )}
    </div>
  );
}
